.material-parallax {
  position: fixed;
  display: flex;
  justify-content: center;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  z-index: 9;
}

.parallax {
  position: absolute;
  background-size: cover;
  overflow: hidden;
  z-index: 1;
  width: 100%;
  height: 100%;
}
.parallax__image {
  display: flex;
  justify-content: center;
  opacity: 0.3;
  z-index: 0;
  width: 100%;
  height: 100%;
  object-position: center;
}
.parallax__image img {
  pointer-events: none;
  width: 100%;
  min-width: 1440px;
  height: 100%;
  max-width: none;
  object-fit: contain;
  object-position: center;
}

.equalizer-content {
  z-index: 999;
  overflow: hidden;
  width: 98%;
  height: 50%;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
}
.equalizer-content.vertical {
  flex-direction: row;
}
.equalizer-content__square {
  border-bottom: 10px solid rgb(4, 217, 255);
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  width: 290px;
  height: 50%;
}
.equalizer-content__square:not(:last-child) {
  margin-right: 1rem;
}

.content-box__body {
  position: relative;
  padding: 2rem;
  margin-bottom: 8rem;
  gap: 2.5rem;
  display: flex;
}
.content-box__column {
  position: relative;
  min-width: 250px;
  height: 300px;
  width: 20%;
}
.content-box__column::before {
  content: "";
  display: block;
  position: absolute;
  box-shadow: 0px 0px 2px 5px rgba(4, 217, 255, 0.4784313725);
  top: -20px;
  left: 30px;
  right: 30px;
  bottom: -20px;
  z-index: -1;
  border-radius: 4px;
  opacity: 0.3;
  transition: all 0.3s ease-in-out;
}
.content-box__column:hover::before {
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  opacity: 0.8;
}
.content-box__column:hover .content-box__image {
  scale: 1.05;
}
.content-box__column:hover .content-box__bg-img {
  transform: translateY(0);
  opacity: 1;
}
.content-box__column:hover .content-box__text {
  transform: translateY(-150%);
}
.content-box__image {
  overflow: hidden;
  transition: all 0.5s ease-in-out;
  height: 100%;
  cursor: pointer;
  border-radius: 4px;
  filter: drop-shadow(0px 0px 10px #392b7c) invert(5%);
}
.content-box__image img {
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  object-fit: cover;
  object-position: center;
  transition: scale 0.4s ease-in-out;
}
.content-box__image img:hover {
  scale: 1.1;
}
.content-box__image svg {
  pointer-events: none;
}
.content-box__bg-img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0;
  transition: opacity 1.5s, box-shadow 0.3s ease-in-out 0.2s;
}
.content-box__svg {
  width: 100%;
  object-fit: cover;
}
.content-box__svg svg {
  position: absolute;
  right: 0;
  max-width: 195px;
}
.content-box__svg svg path {
  opacity: 0;
  stroke: rgb(204, 255, 0);
}
.content-box__text {
  position: absolute;
  font-family: "aa_neon", Helvetica, sans-serif;
  left: 0;
  right: 0;
  font-size: 2.4rem;
  text-align: center;
  bottom: 1rem;
  font-weight: normal;
  text-shadow: 0 0 7px #000;
  display: flex;
  background-color: rgba(11, 8, 19, 0.7333333333);
  justify-content: center;
  color: rgb(4, 217, 255);
  transition: transform 0.4s ease-in-out;
}
.content-box__text svg {
  height: 32px;
}
.content-box__text svg path {
  fill: url(#grad);
  stroke: currentColor;
  stroke-width: 1px;
}

.offer-container {
  flex-direction: column;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.offer-container__title {
  position: relative;
  display: inline;
  padding: 0 5rem 2rem;
  margin-bottom: 6rem;
  font-family: "aa_neon", serif;
  font-size: 3rem;
  font-weight: normal;
  text-align: center;
  line-height: 100%;
  letter-spacing: 0;
  color: rgb(4, 217, 255);
  z-index: 9;
  perspective-origin: 45% 320%;
  transform-style: preserve-3d;
  perspective: 100px;
  filter: drop-shadow(0px 0px 10px #392b7c) invert(5%);
}
@media (max-width: 690px) {
  .offer-container__title {
    font-size: 2.6rem;
  }
}
@media (max-width: 490px) {
  .offer-container__title {
    font-size: 2.2rem;
    padding: 0 2rem 2rem;
    margin: 0 6rem 6rem;
    line-height: 120%;
  }
}
.offer-container__title::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  border-radius: 50%;
  transform: translate(0, 10%);
}
.offer-container__title::after {
  content: "специальное предложение";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 70%;
  font-weight: 900;
  transform: rotateX(15deg) translateZ(-5px);
}
.offer-container__body {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  gap: 2rem;
  height: 100%;
}
@media (max-width: 690px) {
  .offer-container__body {
    grid-template-columns: 1fr;
  }
}
.offer-container__column {
  cursor: pointer;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgb(42, 53, 20);
  padding: 2rem;
  border-radius: 4px;
  filter: drop-shadow(0px 0px 10px #392b7c) invert(5%);
  z-index: 9;
}
.offer-container__column:hover .offer-container__thumbnail {
  opacity: 1;
}
.offer-container__wrapper {
  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
}
.offer-container__thumbnail {
  opacity: 0;
  position: absolute;
  pointer-events: none;
  inset: 0;
  transition: all 0.7s ease-in-out;
  background: linear-gradient(-45deg, rgba(4, 217, 255, 0.5058823529), rgba(23, 14, 49, 0.5058823529) 83%);
  background-size: 200% 200%;
